<template>
  <table class="table table-hover table-sm">
    <thead>
      <tr>
        <th>#</th>
        <th>이름</th>
        <th>level</th>
        <th>boss</th>
        <th>option</th>
        <th>upgrade</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="rune in runeList" :key="rune.id">
        <td v-text="rune.id"></td>
        <td>
          <RuneButton :id="rune.id" disable-tooltip />
        </td>
        <td v-text="rune.level > 0 ? rune.level : '-'"></td>
        <td v-text="rune.boss"></td>
        <td v-html="rune.option.replace(/(?:\r\n|\r|\n)/g, '<br />')"></td>
        <td v-text="rune.upgrade"></td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import { mapState } from "vuex";

import RuneButton from "@/components/diablo2/RuneButton.vue";

export default {
  components: { RuneButton },
  data() {
    return {};
  },
  computed: {
    ...mapState({
      runeList: (state) => state.diablo2.rune.runeList,
    }),
  },
};
</script>

